
.main2_nav {
  width: 100%;
}
.main2_nav .m2_header {
  width: 100%;
  height: 0.98rem;
  padding-top: 0.24rem;
  background: #f5f8fa;
}
.main2_nav .m2_header a {
  color: #808080;
  text-decoration: none;
}
.main2_nav .m2_header a:visited {
  color: #808080;
}
.main2_nav .m2_header .swiper-box {
  padding-left: 0.24rem;
  padding-right: 0.24rem;
}
.main2_nav .m2_header .swiper-box .swiper-wrapper .swiper-slide .opt-item {
  height: 0.69rem;
  line-height: 0.69rem;
  text-align: center;
  border-radius: 25px;
  font-size: 0.46rem;
  color: #999;
  background: #fff;
}
.main2_nav .m2_header .swiper-box .swiper-wrapper .swiper-slide .isActive {
  color: #333;
}
.main2_nav .m2_header .swiper-box .swiper-pagination {
  display: none;
}


